import React from "react";
import { Link, Routes, Route } from "react-router-dom";
import Popover from "@mui/material/Popover";
import LinkedIn from "./images/Linkedin.svg";
import GitHub from "./images/GitHub.svg";
import PaperClip from "./images/PaperClip.svg";
import Suitcase from "./images/Suitcase.svg";
import Person from "./images/Person.svg";
import House from "./images/House.svg";
import "./Header.css";
import { useState } from "react";

function Header() {
  const [open, setOpen] = useState<boolean>(false);
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(
    null
  );

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <header className="Social-Wrapper" id="Header">
      <a className="Social" href="https://www.linkedin.com/in/ethan-v-nguyen">
        <img className="Social" src={LinkedIn} alt="" />
        LINKEDIN
      </a>
      <a className="Social" href="https://github.com/FunniNam3">
        <img className="Social" src={GitHub} alt="" />
        GITHUB
      </a>
      {/* <a
        className="Social"
        href="https://docs.google.com/document/d/1tAgY0dw4jPYVEkNXJ4KTaeLv23DfrBgvN0l1JGnyhEM/export?format=pdf"
      >
        <img className="Social" src={PaperClip} alt="" />
        RESUME
      </a> */}
      <div>
        <button onClick={handleClick} className="Social">
          <img className="Social" src={PaperClip} alt="" />
          RESUME
        </button>
        <Popover
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
          open={Boolean(anchorEl)}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              gap: "2vmin",
              padding: "2vmin",
            }}
          >
            <a
              className="Social"
              href="https://docs.google.com/document/d/1tAgY0dw4jPYVEkNXJ4KTaeLv23DfrBgvN0l1JGnyhEM/export?format=docx"
            >
              <img className="Social" src={PaperClip} alt="" />
              Software
            </a>
            <a
              className="Social"
              href="https://docs.google.com/document/d/1msTHmNudJgS8PLMuBNoJVpGe64rI2kCZbaakOpVHzng/export?format=docx"
            >
              <img className="Social" src={PaperClip} alt="" />
              Hardware
            </a>
          </div>
        </Popover>
      </div>

      <Routes>
        <Route
          path="*"
          element={
            <Link className="Social" to="/Projects">
              <img className="Social" src={Suitcase} alt="" />
              PROJECTS
            </Link>
          }
        />
        <Route
          path="/Projects"
          element={
            <Link className="Social" to="/AboutMe">
              <img className="Social" src={Person} alt="" />
              ABOUT ME
            </Link>
          }
        />
      </Routes>
      <Routes>
        <Route
          path="/"
          element={
            <Link className="Social" to="/AboutMe">
              <img className="Social" src={Person} alt="" />
              ABOUT ME
            </Link>
          }
        />
        <Route
          path="*"
          element={
            <Link className="Social" to="/">
              <img className="Social" src={House} alt="" />
              HOME
            </Link>
          }
        />
      </Routes>
    </header>
  );
}

export default Header;
