import { useState } from "react";
import "./Timeline.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGamepad,
  faListCheck,
  faSchool,
  faSuitcase,
  faFish,
  faVideo,
  faTimeline,
  faPlug,
  faHexagonNodes,
  faWifi,
} from "@fortawesome/free-solid-svg-icons";

export interface TimelineLink {
  link: string;
  name: string;
}

export interface TimelineItemData {
  title: string;
  date: string;
  description: string;
  type?: string;
  selector?: string;
  tags?: string[];
  imgPath?: string;
  links?: TimelineLink[];
}

const iconMap: Record<string, any> = {
  faSchool: faSchool,
  faSuitcase: faSuitcase,
  faListCheck: faListCheck,
  faGamepad: faGamepad,
  faFish: faFish,
  faVideo: faVideo,
  faTimeline: faTimeline,
  faPlug: faPlug,
  faHexagonNodes: faHexagonNodes,
  faWifi: faWifi,
};

export function Container({
  id,
  itemKey,
  children,
  style,
  className,
  hidden,
}: {
  id?: string | number;
  itemKey: string;
  className?: string;
  children?: React.ReactNode;
  style?: React.CSSProperties;
  hidden?: boolean;
}) {
  return (
    <div
      id={String(id)}
      key={itemKey ? itemKey : id}
      className={`Container ${className}`}
      style={style}
      hidden={hidden}
    >
      {children}
    </div>
  );
}

export function TimelineTags({ tags }: { tags?: string[] }): JSX.Element {
  if (tags) {
    return (
      <div style={{ display: "flex", flexDirection: "row", gap: "0.5vmin" }}>
        {tags.map((item, index) => {
          return (
            <Container
              className="TimelineTag"
              key={`tag ${index}`}
              itemKey={`tag ${index}`}
              style={{
                width: "fit-content",
                borderRadius: "10vmin",
                padding: "0.5vmin 1vmin",
              }}
            >
              <h2 className="TimelineTag">{item}</h2>
            </Container>
          );
        })}
      </div>
    );
  } else {
    return <></>;
  }
}

export function TimelineLinks({
  links,
}: {
  links?: TimelineLink[];
}): JSX.Element {
  if (links) {
    return (
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          gap: "0.5vmin",
          pointerEvents: "auto",
        }}
      >
        {links.map((item, index) => {
          return (
            <a
              key={index}
              className="TimelineLink"
              href={item.link}
              style={{
                width: "fit-content",
                borderRadius: "10vmin",
                padding: "0.5vmin 1vmin",
              }}
              target="_/"
            >
              {item.name}
            </a>
          );
        })}
      </div>
    );
  } else {
    return <></>;
  }
}

export function TimelineItem({
  id,
  itemKey,
  className,
  data,
  hidden,
  title,
  date,
  image,
  alignRight,
}: {
  id?: string | number;
  itemKey: string;
  className?: string;
  data?: TimelineItemData;
  hidden?: boolean;
  title?: string;
  date?: string;
  image?: string;
  alignRight?: boolean;
}): JSX.Element {
  if (data) {
    if (hidden) {
      return <div className="TimelineItemEmpty"></div>;
    }
    return (
      <Container
        itemKey={itemKey}
        id={id}
        className={`TimelineItem ${className}`}
        style={{
          backgroundColor: "#FFF",
          flex: 1,
          gap: "1vmin",
          display: "flex",
          flexDirection: "column",
          justifyContent: "flex-start",
          alignItems: "flex-start",
        }}
        hidden={hidden}
      >
        <TimelineTags tags={data.tags} />
        <p>{data.description}</p>
        <TimelineLinks links={data.links} />
      </Container>
    );
  } else {
    return (
      <div
        className="TimelineItemTitle"
        style={{
          flexDirection: alignRight ? "row" : "row-reverse",
          gap: "2vmin",
        }}
      >
        {image && (
          <img
            className={className}
            src={image}
            hidden={hidden}
            style={{ maxWidth: "50%", flex: 1, height: "auto" }}
          />
        )}
        <div style={{ flex: 1 }}>
          <h2 style={{ textAlign: alignRight ? "right" : "left" }}>{title}</h2>
          <h3 style={{ textAlign: alignRight ? "right" : "left" }}>{date}</h3>
        </div>
      </div>
    );
  }
}

export function TimelineSelector({
  icon,
  hide,
  setHide,
}: {
  icon?: string;
  hide: boolean;
  setHide: React.Dispatch<React.SetStateAction<boolean>>;
}): JSX.Element {
  return (
    <div
      className="TimeLineSelector"
      onClick={() => {
        setHide(!hide);
      }}
    >
      <FontAwesomeIcon
        icon={icon ? iconMap[icon] : faSuitcase}
        style={{ fontSize: "5vw", margin: 0, textAlign: "center" }}
      />
    </div>
  );
}

export function TimelineItemContainer({
  item,
  index,
}: {
  item: TimelineItemData;
  index: number;
}) {
  const [hidden, setHidden] = useState<boolean>(true);
  const [animClass, setAnimClass] = useState<string>("");
  const [imageClass, setImgClass] = useState<string>("");

  const isOpposite = item.type === "Opposite";

  const handleToggle = () => {
    if (hidden) {
      setAnimClass(isOpposite ? "FadeInRight" : "FadeInLeft");
      setImgClass(!isOpposite ? "FadeInRight" : "FadeInLeft");
      setHidden(false);
    } else {
      setAnimClass(isOpposite ? "FadeOutRight" : "FadeOutLeft");
      setImgClass(!isOpposite ? "FadeOutRight" : "FadeOutLeft");

      setTimeout(() => {
        setHidden(true);
      }, 600);
    }
  };

  return (
    <div key={index} className="TimelineItemContainer">
      {isOpposite || typeof item.type == null ? (
        <>
          <TimelineItem
            itemKey={`TimelineEmptyItem${String(index)}`}
            className={imageClass}
            date={item.date}
            title={item.title}
            image={item.imgPath}
            hidden={hidden}
            alignRight
          />
          <TimelineSelector
            icon={item.selector}
            hide={hidden}
            setHide={handleToggle}
          />
          <TimelineItem
            itemKey={`TimelineItem${String(index)}`}
            hidden={hidden}
            className={animClass}
            data={item}
          />
        </>
      ) : (
        <>
          <TimelineItem
            itemKey={`TimelineItem${String(index)}`}
            data={item}
            className={animClass}
            hidden={hidden}
          />
          <TimelineSelector
            icon={item.selector}
            hide={hidden}
            setHide={handleToggle}
          />
          <TimelineItem
            itemKey={`TimelineEmptyItem${String(index)}`}
            className={imageClass}
            date={item.date}
            title={item.title}
            image={item.imgPath}
            hidden={hidden}
          />
        </>
      )}
    </div>
  );
}

export function Timeline({
  data,
}: {
  data?: TimelineItemData[];
  children?: React.ReactNode;
}): JSX.Element {
  if (data) {
    return (
      <div className="Timeline">
        <div className="TimelineLine"></div>
        {data.map((item, index) => {
          return (
            <TimelineItemContainer key={index} item={item} index={index} />
          );
        })}
      </div>
    );
  } else {
    return (
      <div className="Timeline">
        <h1>YOU NO PUT NOTHING</h1>
      </div>
    );
  }
}
